import React from 'react'

const MapsDescription = () => <div className="description">
    <p>
        Die Visualisierung zeigt mit Hilfe von Karten der Schweiz die Entwicklung der Unflle im Verlauf der letzten 24 Jahre (bis 2016) aufgesplittet nach Kantonen.
        Dabei kann im Dropdown Menu gewechselt werden zwischen den relativen Werten "Unflle pro 1000 Fahrzeuge" und den absoluten Werten "Unflle insgesamt".
        Zudem lsst die dritte Auswahlmglichkeit "Zugelassene Fahrzeuge" einen Vergleich der Unfallsentwicklung zur Anzahl Fahrzeugen zu.
        Beim hovern ber die Schweizerkarten werden die Daten fr den jeweiligen Kanton angezeigt.
    </p>
    <h4>Quellen</h4>
    <ul className="sources">
        <li><a href="https://www.bfs.admin.ch/bfs/de/home/statistiken/kataloge-datenbanken/tabellen.assetdetail.4402685.html" target="_blank" rel="noopener noreferrer">Strassenfahrzeugbestand: Motorfahrzeuge ab 1990</a>, Bundesamt fr Statistik, abgerufen am 29. Mai 2018</li>
        <li><a href="https://www.bfs.admin.ch/asset/de/px-x-1106010100_101" target="_blank" rel="noopener noreferrer">Strassenverkehrsunflle: Unflle mit Personenschaden nach Kanton, Unfallschwere und Unfallort</a>, Bundesamt fr Statistik, abgerufen am 22. Mai 2018</li>
    </ul>
</div>


export default MapsDescription


